import { Injectable } from "@angular/core";
import { Storage } from "@ionic/storage";

@Injectable()
export class SessionProvider {
  constructor(private storage: Storage) { }

  public newUser(data) {
    console.log('set  user: ', data)
    this.storage.set("user", data);
  }
}



