import { Component } from "@angular/core";
import { NavController, LoadingController, Events } from "ionic-angular";
import { config } from "../../config";
import { OfflineProvider } from "../../providers/offline/offline";
import { TopicPage } from "../topic/topic";
import { ExamsubjectsPage } from "../examsubjects/examsubjects";
@Component({
  selector: "page-home",
  templateUrl: "home.html"
})
export class HomePage {
  config: any;
  subjects: any[];
  loaded: Promise<boolean>;
  constructor(
    public navCtrl: NavController,
    private offlineProvider: OfflineProvider,
    private loadingCtrl: LoadingController,
    public events: Events
  ) {
    this.config = config;

    let loader = this.loadingCtrl.create({
      content: "Initiating DB"
    });
    loader.present().then(() => {
      console.log('loader presented')
      this.offlineProvider.getDatabaseState().subscribe(rdy => {
        if (rdy) {
          loader.dismiss();
          console.log("here");
          this.offlineProvider
            .fetchSubjects()
            .then(data => {
              this.subjects = data;
              console.log(data);
              this.loaded = Promise.resolve(true);

            })
            .catch(async err => {
              console.log("err", err);
              await this.offlineProvider.setupDb()
              window.location.reload();
            });
        }
      });
    });

  }

  gotoTopicPage(i, subjectFiltered) {
    let subject = subjectFiltered;
    this.navCtrl.push(
      TopicPage,
      {
        subject: subject,
        questionType: "practice"
      },
      {
        animate: true,
        animation: "transition-ios",
        direction: "forward"
      }
    );
  }

  gotoSubjects() {
    this.navCtrl.push(ExamsubjectsPage, {
      animate: true,
      animation: "transition-ios",
      direction: "forward"
    });
  }
  ionViewDidLoad() {
    this.events.publish("play_bg_audio");
  }
}



