import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  AlertController
} from "ionic-angular";
import { OfflineProvider } from "../../providers/offline/offline";
import { QuizPage } from "../quiz/quiz";

/**
 * Generated class for the QuizresultPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-quizresult",
  templateUrl: "quizresult.html"
})
export class QuizresultPage {
  result: any = {};
  loaded: Promise<boolean>;
  evaluation = {};
  questionType: string;
  subject: any;
  questions: any;
  hideEvaluation: boolean = true;
  mostFrequentTopic;
  frequentTopic: string;
  topicLoaded: boolean = false;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public offlineProvider: OfflineProvider,
    public alertController: AlertController
  ) {
    this.result = this.navParams.get("result");
    this.evaluation = this.navParams.get("evaluation");
    this.questionType = this.navParams.get("questionType");
    this.subject = this.navParams.get("subject");
    this.loaded = Promise.resolve(true);
  }

  ionViewDidLoad() {
    let counts = {};
    let compare = 0;
    for (let i = 0; i < this.result.questions.length; i++) {
      let question_correct = this.result.questions[i].correct;
      let topic_id = this.result.questions[i].topic_id;
      console.log(question_correct, topic_id);
      if (question_correct == "no") {
        if (counts[topic_id] === undefined) {
          counts[topic_id] = 1;
        } else {
          counts[topic_id] = counts[topic_id] + 1;
        }

        if (counts[topic_id] > compare) {
          compare = counts[topic_id];
          this.mostFrequentTopic = topic_id;
          if (this.questionType == "normal") {
            this.finalEvaluation();
          }
        }
      }
    }
  }

  finalEvaluation() {
    this.offlineProvider.fetchTopicByID(this.mostFrequentTopic).then(topic => {
      this.frequentTopic = topic;
      this.topicLoaded = true;
    });
  }

  learnMore() {
    this.offlineProvider
      .fetchQuestionForEvalutation(this.mostFrequentTopic)
      .then(questions => {
        console.log(questions);
        this.navCtrl.setRoot(
          QuizPage,
          {
            test_type: 0,
            subject: this.subject,
            questions: questions,
            topic: this.frequentTopic,
            questionType: "practice"
          },
          {
            animate: true,
            animation: "transition-ios",
            direction: "forward"
          }
        );
      });
  }

  rateUs() {
    const alert = this.alertController.create({
      title: "Do you love simbibot?",
      buttons: [
        {
          text: "No",
          cssClass: "buttonAlert",
          handler: data => {
            window.open(`mailto:az.brimatel@gmail.com`, "_system");
          }
        },
        {
          text: "Yes",
          cssClass: "buttonAlert",
          handler: data => {
            window.open(
              "https://play.google.com/store/apps/details?id=app.simbibotv2.com&hl=en_US",
              "_system"
            );
          }
        }
      ]
    });

    alert.present();
  }

  contactUs() {
    window.open(`mailto:talktosimbibot@gmail.com`, "_system");
  }
}



