import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  LoadingController
} from "ionic-angular";
import { OfflineProvider } from "../../providers/offline/offline";
import { config } from "../../config";
import { QuizPage } from "../quiz/quiz";

/**
 * Generated class for the ExamsubjectsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-examsubjects",
  templateUrl: "examsubjects.html"
})
export class ExamsubjectsPage {
  config: any;
  loaded: Promise<boolean>;
  subjects: any;
  selectedSubject: any;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public offlineProvider: OfflineProvider,
    public loadingCtrl: LoadingController
  ) {
    this.config = config;
    this.fetchExamSubject();
  }

  ionViewDidLoad() { }

  fetchExamSubject() {
    this.offlineProvider
      .fetchSuperExamByID(this.config.super_exam_id)
      .then(data => {
        console.log(data);
        this.subjects = JSON.parse(data.subjects_list);
        this.loaded = Promise.resolve(true);
      });
  }

  proceedToQuiz() {
    const loader = this.loadingCtrl.create({
      content: "Loading..."
    });

    loader.present();
    this.offlineProvider
      .fetchQuestionFromExamList(
        this.selectedSubject.id,
        this.config.super_exam_id
      )
      .then(data => {
        this.offlineProvider
          .fetchQuestionFromExamListCount(
            this.selectedSubject.id,
            this.config.super_exam_id
          ).then(count => {
            loader.dismiss();
            this.navCtrl.setRoot(QuizPage, {
              subject: this.selectedSubject,
              test_type: 2,
              questions: data,
              questionType: "normal",
              count
            });
          })
      });
  }
}



