import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ModalController
} from "ionic-angular";
import { OfflineProvider } from "../../providers/offline/offline";
import { OnboardingPage } from "../onboarding/onboarding";
import { QuizPage } from "../quiz/quiz";

/**
 * Generated class for the TopicPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-topic",
  templateUrl: "topic.html"
})
export class TopicPage {
  subject: any;
  questionType: string;
  topics: any[] = [];
  loaded: Promise<boolean>;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public offlineProvider: OfflineProvider,
    public modalCtrl: ModalController
  ) {
    this.subject = this.navParams.get("subject");
    this.questionType = this.navParams.get("questionType");
    this.fetchTopics(this.subject.id);
  }

  ionViewDidLoad() { }

  fetchTopics(id) {
    this.offlineProvider.fetchTopics(id).then(data => {
      this.topics = data;
      this.loaded = Promise.resolve(true);
    });
  }

  openTopicInfo(i) {
    let modal = this.modalCtrl.create(OnboardingPage, {
      subject: this.subject,
      topic: this.topics[i]
    });

    modal.present();

    modal.onDidDismiss(data => {
      if (data) {
        if (data.take_test != null) {
          this.navCtrl.setRoot(QuizPage, {
            subject: this.subject,
            topic: this.topics[i],
            test_type: data.test_type,
            questions: data.questions,
            questionType: this.questionType
          });
        }
      }
    });
  }
}



