import { Component, ViewChild } from "@angular/core";
import {
  Nav,
  Platform,
  MenuController,
  Events,
  App,
  AlertController
} from "ionic-angular";
import { Device } from "@ionic-native/device";
import { LocalNotifications } from '@ionic-native/local-notifications';
// import { TealiumInstallReferrer } from '@ionic-native/tealium-installreferrer/ngx';
// import { InstallReferrer } from 'install-referrer'

import { StatusBar } from "@ionic-native/status-bar";
import { SplashScreen } from "@ionic-native/splash-screen";
import { Storage } from "@ionic/storage";

import { HomePage } from "../pages/home/home";
import { SyllabusPage } from "../pages/syllabus/syllabus";
import { ListPage } from "../pages/list/list";
import { LoginPage } from "../pages/login/login";
import { OfflineProvider } from "../providers/offline/offline";
import { config } from "../config";
import { ExamsubjectsPage } from "../pages/examsubjects/examsubjects";
import { AboutPage } from "../pages/about/about";
import { StoryBookPage } from "../pages/storybook/storybook";
import { QuizresultPage } from "../pages/quizresult/quizresult";
import { QuizPage } from "../pages/quiz/quiz";
import { NativeAudio } from "@ionic-native/native-audio";
import { SubscriptionProvider } from "../providers/subscription/subscription";
import { SubscriptionPage } from "../pages/subscription/subscription";
import { UpdatesPage } from "../pages/updates/updates";
import { NotificationsPage } from "../pages/notifications/notifications";

declare global {
  interface Window {
    Pusher: any;
    Notification: {
      permission: any
    }
  }
}

let Pusher = window.Pusher;

@Component({
  templateUrl: "app.html"
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;

  rootPage: any;

  switch: boolean = false;

  pages: Array<{ title: string; component: any; icon: string }>;

  hasSubscribed: boolean = false;

  updatesPage: any = UpdatesPage

  aboutPage: any = AboutPage

  subscriptionPage: any = SubscriptionPage

  constructor(
    public platform: Platform,
    public statusBar: StatusBar,
    public splashScreen: SplashScreen,
    public storage: Storage,
    private menuController: MenuController,
    private offlineProvider: OfflineProvider,
    private events: Events,
    private app: App,
    private alertCtrl: AlertController,
    private audio: NativeAudio,
    private localNotifications: LocalNotifications,
    // private installReferrer: TealiumInstallReferrer,
    // private _installReferrer: InstallReferrer,
  ) {
    this.initializeApp();

    // used for an example of ngFor and navigation
    this.pages = [
      { title: "Home", component: HomePage, icon: "home" },
      {
        title: "Pratice " + config.version_name.toUpperCase(),
        component: ExamsubjectsPage,
        icon: "create"
      },
      {
        title: "Sweet Sixteen",
        component: StoryBookPage,
        icon: "book"
      },
      // {
      //   title: "Syllabus",
      //   component: SyllabusPage,
      //   icon: "book"
      // },
      {
        title: "Notifications",
        component: NotificationsPage,
        icon: "notifications"
      }
    ];
  }

  initializeApp() {
    this.platform.ready().then(() => {
      const myapp = this;

      // initialize install referrer
      // console.log('referrer ', this.installReferrer)

      // this.installReferrer.setPersistent("referrer");
      // this.installReferrer.setVolatile("referrer");

      // this._installReferrer.getReferrer()
      //   .then(data => {
      //     // data is a array with all parameters received
      //     console.log('data ', data)
      //   })
      //   .catch(err => { });

      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      const pusher = new window.Pusher('b882ddf2f684e4162fd3', {
        cluster: 'eu'
      });
      let channel = pusher.subscribe('general-message-channel');
      // console.log('channel ', channel)
      channel.bind('App\\Events\\GeneralInboxAdded', function (data) {
        // console.log('data from pusher', data, ' trigger ', this);

        myapp.displayNotification(data);
      });

      pusher.connection.bind('error', function (err) {
        console.log('pusher error ', err);
        if (err.error.data.code === 4004) {
          console.log('>>> detected limit error');
        }
      });

      this.keepSession();
      this.checkIfDbhasbeenCopied();
      // this.offlineProvider.unzipDatabase();
      this.statusBar.backgroundColorByHexString("#36BFE9");
      this.splashScreen.hide();

      this.events.subscribe("db_setup", () => {
        console.log("Event when db has finished copying");
        console.log("Waiting 3 seconds");
        setTimeout(() => {
          this.checkIfDbhasbeenCopied();
        }, 3000);
      });

      this.events.subscribe('user_subscribed', () => {
        console.log('user has subscripbed');
        this.hasSubscribed = true;
      })

      this.storage.get('subscribed').then(val => this.hasSubscribed = val)

      this.platform.registerBackButtonAction(() => {
        const overlayView = this.app._appRoot._overlayPortal._views[0];
        const view = this.nav.getActive();
        const viewCtrl = this.app._appRoot._modalPortal.getActive();
        console.log('back')
        try {
          viewCtrl.dismiss();
          console.log('dismiss view')
        } catch (e) {
          console.log('dismiss view error ', e)
          if (overlayView && overlayView.dismiss) {
            overlayView.dismiss();
          } else {
            let nav = this.app.getActiveNav();
            if (nav.canGoBack()) {
              nav.pop();
            } else if (view.instance instanceof HomePage) {
              this.alertCtrl
                .create({
                  subTitle: "Do you want to quit?",
                  buttons: [
                    {
                      text: "cancel",
                      role: "Cancel"
                    },
                    {
                      text: "ok",
                      handler: () => {
                        this.platform.exitApp();
                      }
                    }
                  ]
                })
                .present();
            } else if (
              viewCtrl.instance instanceof QuizresultPage ||
              viewCtrl.instance instanceof QuizPage
            ) {
              viewCtrl.dismiss();
            } else {
              this.app.getRootNav().setRoot(
                QuizresultPage,
                {},
                {
                  animate: true,
                  animation: "transition",
                  duration: 500,
                  direction: "back"
                }
              );
            }
          }
        }
      });



      this.audio
        .preloadComplex("themesong", "assets/audio/themesong.mp3", 1, 1, 0)
        .then(
          data => {
            this.audio.preloadSimple("correct", "assets/audio/correct.mp3");
            this.audio.preloadSimple("wrong", "assets/audio/wrong.mp3");
            console.log(data);
          },
          err => {
            console.log("error while preloading.");
          }
        );

      this.events.subscribe("play_bg_audio", () => {
        if (this.switch) {
          this.audio.loop("themesong").then(() => {
            console.log("Playing theme song");
          });
        }
      });

      this.events.subscribe("reduce_volume", () => {
        this.audio.setVolumeForComplexAsset("themesong", 0.4);
      });
    });
  }

  displayNotification(data) {
    let inboxMessage = data.inboxMessage

    // If the user accepts, let's create a notification
    this.localNotifications.hasPermission().then(permission => {
      if (permission) {
        this.localNotifications.schedule({
          id: inboxMessage.id,
          title: inboxMessage.title,
          text: inboxMessage.content,
          icon: '../assets/imgs/Simbibot-favicon.png',
          data: { secret: 'key_data' }
        });
      } else {
        this.localNotifications.requestPermission().then(permission => {
          // console.log('requested ', permission)
          if (permission) {
            this.localNotifications.schedule({
              id: inboxMessage.id,
              title: inboxMessage.title,
              text: inboxMessage.content,
              icon: '../assets/imgs/Simbibot-favicon.png',
              data: { secret: 'key_data' }
            });
          }
        })
      }
    })


    if ('Notifications' in window) {
      let notification = new Notification(inboxMessage.title, { body: inboxMessage.content, icon: '../assets/imgs/Simbibot-favicon.png' });
      notification.onclick = function (event) {
        event.preventDefault();
        console.log('notification clicked ', event, inboxMessage)
      }
    }
  }

  openPage(page) {
    this.nav.setRoot(page.component);
  }

  openPageManually(component) {
    this.nav.setRoot(component);
  }

  async keepSession() {
    let user = await this.storage.get("user");
    console.log('get user: ', user)
    if (user != null) {
      let subscribed = await this.storage.get("subscribed");
      console.log("subscribe", subscribed);
      if (subscribed != null && subscribed == true) {
        return (this.rootPage = HomePage);
      } else {
        let expiryDate = await this.storage.get("expiry_date");

        // Holds if user has either shared or not.
        let status = await this.storage.get("status");
        console.log('status ', status)

        let today = new Date();
        expiryDate = new Date(expiryDate);

        console.log('today ', today.getDate(), ' expiry date ', expiryDate.getDate())
        if (
          (status != null && status == "shared") ||
          (status != null && status == "notshared")
        ) {
          console.log('expired ', today.getDate() > expiryDate.getDate())
          if (today.getDate() > expiryDate.getDate()) {
            this.rootPage = SubscriptionPage;
          } else {
            this.rootPage = HomePage;
          }
        } else {
          let date = new Date();
          // Change to 1 day
          date.setDate(date.getDate() + 1);
          console.log('set expiry date: ', date.getDate())
          this.storage.set("expiry_date", date);
          this.storage.set("status", "notshared");

          this.rootPage = HomePage;
        }
      }
    } else {
      console.log('get user: ', user)
      this.rootPage = LoginPage;
    }
  }

  checkIfDbhasbeenCopied() {
    this.storage.get("db_copied").then(val => {
      if (val == null) {
        this.offlineProvider.setupDb();
      } else {
        this.events.publish("db_hascopied");
      }
    });
  }

  offSound() {
    console.log('sound-off: ', this.switch)
    this.switch = !this.switch;
    if (!this.switch) {
      this.storage.set("sound-off", this.switch).then(val => {
        this.audio.stop("themesong");
      });
    } else {
      this.storage.set("sound-off", this.switch).then(_ => {
        this.audio.stop("themesong");
        this.audio.loop("themesong").then(() => {
          console.log("Playing theme song");
        });
      });
    }
  }

  openWhatssapLink() {
    window.open("whatsapp://chat?code=KtybtfGE9QcHbDUEMY6JMc", "_system");
  }

  logOut() {
    this.menuController.close();
    this.storage.clear();
    this.nav.setRoot(LoginPage, null, {
      animate: true,
      animation: "transition-ios",
      direction: "back"
    });
  }
}



