import { Component, ViewChild } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  Slides,
  Events,
  LoadingController,
  ToastController,
  AlertController,
  MenuController
} from "ionic-angular";
import { Storage } from "@ionic/storage";
import { HomePage } from "../home/home";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Device } from "@ionic-native/device";
import { AppPreferences } from '@ionic-native/app-preferences/ngx';

import { SubscriptionProvider } from "../../providers/subscription/subscription";
import { AuthProvider } from "../../providers/auth/auth";
import { NetworkProvider } from "../../providers/network/network";
import { SessionProvider } from "../../providers/session/session";
import { InappbrowserProvider } from '../../providers/inappbrowser/inappbrowser';
import { config } from '../../config'

/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-login",
  templateUrl: "login.html"
})
export class LoginPage {
  email: any;
  password: any;
  showLogin: boolean;
  user: any;
  currentMessage: string;
  registerUserBody: FormGroup;
  @ViewChild("slides") slides: Slides;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private _authProvider: AuthProvider,
    private loadingCtrl: LoadingController,
    private formBuilder: FormBuilder,
    private network: NetworkProvider,
    private toast: ToastController,
    private alertController: AlertController,
    private session: SessionProvider,
    private menuController: MenuController,
    private storage: Storage,
    private inappBrowser: InappbrowserProvider,
    private subscription: SubscriptionProvider,
    private device: Device,
    private events: Events,
    public appPreferences: AppPreferences
  ) {
    this.currentMessage = "Can I get your email?";
    this.registerUserBody = this.formBuilder.group({
      email: ["", [Validators.required, Validators.email]],
      firstname: ["", [Validators.required]],
      lastname: ["", [Validators.required]],
      phone: ["", [Validators.required, Validators.minLength(11)]],
      password: ["", [Validators.required, Validators.minLength(6)]]
    });
    this.menuController.swipeEnable(false);
  }

  ionViewDidLoad() {
    this.slides.lockSwipes(true);
    // set last updated time to now
    this.storage.set('last_updated_time', new Date().toISOString())
    // console.log(this.appPreferences)
    // this.appPreferences.fetch('referrer').then((res) => { console.log('referrer ', res); });
  }

  forgotPassword() {
    console.log('forgot password')
    // window.open(config.forgotPassword, "_blank")
    this.inappBrowser.openBrowser(config.forgotPassword);
  }

  verifyEmailAddress() {
    // skip all actions if user has not entered their email
    if (!this.email || this.email.lenght === 0) return;

    let loader = this.loadingCtrl.create({
      cssClass: "my-loading"
    });
    loader.present();
    this._authProvider.checkEmail(this.email).subscribe(
      (response: any) => {
        if (response.message == true) {
          this.user = response.user;
          this.currentMessage = `Welcome back, ${response.user.firstname}`;
          this.showLogin = true;
          loader.dismiss();
        } else {
          this.registerUserBody.controls.email.setValue(this.email);
          this.next();
          loader.dismiss();
        }
      },
      (err: any) => {
        console.log(err);
        loader.dismiss();
      }
    );
  }

  loginUser() {
    if (!this.network.isOffline()) {
      let loader = this.loadingCtrl.create({
        cssClass: "my-loading"
      });
      loader.present();
      this._authProvider.login(this.email, this.password).subscribe(
        async (response: any) => {
          this.session.newUser(response.user);

          // check if user has subscribed
          let device_id = this.device.uuid;
          console.log('device id ', device_id)
          this.subscription.checkIfUserHasSubscribed(device_id).then(resp => {
            resp.subscribe(
              (response: any) => {
                loader.dismiss();
                this.storage.set("subscribed", true);
                this.storage.set(
                  "expiry_date",
                  new Date(new Date().setFullYear(new Date().getFullYear() + 1))
                );
                this.navCtrl.setRoot(HomePage, null, {
                  animate: true,
                  animation: "transition-ios",
                  direction: "forward"
                });
                console.log('user has subscribed published');
                this.events.publish('user_subscribed')
              },
              (err: any) => {
                console.log('user has not subscribed')
              }
            );
          });

          loader.dismiss();

          this.navCtrl.setRoot(HomePage, null, {
            animate: true,
            animation: "transition-ios",
            direction: "forward"
          });
        },
        (err: any) => {
          this.alertMessage(err.error.message);
          loader.dismiss();
        }
      );
    } else {
      this.showNoInternetConnection();
    }
  }

  registerUser() {
    if (!this.network.isOffline()) {
      let loader = this.loadingCtrl.create({
        cssClass: "my-loading"
      });
      loader.present();
      this._authProvider.signup(this.registerUserBody.value).subscribe(
        async (response: any) => {
          await this.session.newUser(response.user);

          loader.dismiss();

          // const date = new Date();
          // date.setDate(date.getMinutes() + 3)
          // console.log(date.getUTCDate())
          // await this.storage.set("expiry_date", date);
          console.log(this.storage.get('user'))

          this.navCtrl.setRoot(HomePage, null, {
            animate: true,
            animation: "transition-ios",
            direction: "forward"
          });
          // window.location.reload();
        },
        (err: any) => {
          this.alertMessage(err.error.message);
          loader.dismiss();
        }
      );
    } else {
      this.showNoInternetConnection();
    }
  }

  next() {
    this.slides.lockSwipes(false);
    this.slides.slideNext();
    this.slides.lockSwipes(true);
  }

  showNoInternetConnection() {
    this.toast
      .create({
        message: "No Internet Connection.",
        cssClass: "errorToast",
        duration: 3000
      })
      .present();
  }

  alertMessage(message) {
    this.alertController
      .create({
        subTitle: "Opps, an error occured",
        message: message,
        buttons: ["ok"]
      })
      .present();
  }
}



